
/* style for the reusable card components */
/* primary use is the work page */

.cardBody img{
  width:100%;
  border-radius: 8px 8px 0 0;
}

.cardfooter{
  padding:3.5% 4.5% 5.5%;
}
.cardfooter span{
  font-weight: normal;
  font-size:.8em;
  display:block;
  padding:5px 0 10px;
}

a .cardfooter {
  font-weight: bold;
  color:#333;
}

a.cardwrapper{
  width:31%;
  background-color: #fff;
  margin:1% 2% 1% 0;
  text-decoration: none;
  box-shadow: 1px 1px 10px 4px #ccc;
  border-radius: 8px;
}

a.cardwrapper:hover{
  box-shadow: 1px 1px 10px 4px #ccc;
  transform: scale(1.01);
}

.cardFooter{
  background-color: #ececec;
  overflow: auto;
  white-space: nowrap;
  overflow-x: scroll;
  padding:40px 8.333%;
  margin-top:50px;
}

.cardFooter .cardwrapper{
  width:150px;
  display: inline-block;
}

.cardFooter .cardwrapper h6{
  font-size:.8em;
}


@media only screen and (max-width: 767px) {
  a.cardwrapper {
    width: 48%;
  }
  
}

@media only screen and (max-width : 480px) {
  a.cardwrapper {
    width: 48%;
    margin:2% 2% 3% 0;
   }
  }
